<template lang="html">
  <div>
    <div id="footer" class="footer text-center unselectable">
      <small>Copyright &copy;{{ date }} John Noonan. <a href="#privacyModal" data-toggle="modal" data-target="#privacyModal">View the privacy policy here</a></small>
    </div>
      <div
        class="modal fade"
        id="privacyModal"
        tabindex="-1"
        role="dialog"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content align-middle">
            <div class="modal-header">
              <h3 class="modal-title" id="popupModalLabel">Privacy Policy</h3>
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <p>This website uses Clicky to log and analyze its traffic. No cookies are used, the only information logged is:
              <ul>
                <li>URL & Title of pages viewed</li>
                <li>Referrer</li>
                <li>Anonymized IP address</li>
              </ul> 
               You may review <a href="https://clicky.com/terms/privacy" target="_blank" rel="noopener noreferrer">Clicky's privacy policy here</a></p>
            </div>
          </div>
        </div>
      </div>
</div>
  
</template>

<script>
import $ from "jquery";

export default {
  name: "myFooter",
  computed: {
    date: function() {
      return new Date().getFullYear();
    }
  },
  mounted() {
    $("#privacyModal")
      .on("show.bs.modal", () => {
        $('#privacyModal').css('z-index', 1001);

      })
      .on("hidden.bs.modal", () => {
        $('#privacyModal').css('z-index', -1001);
      });
  }
};
</script>

<style scoped src="bootstrap/dist/css/bootstrap.css"></style>
<style lang="css" scoped>
div.footer {
  z-index: 100;
  height: 2em;
  height: 2em;
  line-height: 2em;
  position: absolute;
  overflow: hidden;
  width: 100%;
  left: 0;
  bottom: 0;
  color: var(--midCol);
}

a {
  color: var(--midCol);
  text-decoration-thickness: 15%;
  text-underline-offset: .2em;
}

a:hover {
  color: gray;
}

.modal-dialog {
  /* Width */
  max-width: 100%;
  width: auto !important;
/*  display: inline-block;*/
}

.modal {
  z-index: -1001;
  display: flex !important;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.2);
}

.modal-content {
  background-color: var(--darkCol);
  color: white;
}

