<template lang="html">
  <nav
    id="navContainer"
    class="navbar navbar-expand-lg header navbar-light navbar-default"
    :class="{ moreHeight: !this.$root.isMobile }"
  >
    <button
      class="navbar-toggler icon-bar"
      type="button"
      data-toggle="collapse"
      data-target="#navbarNavAltMarkup"
      aria-controls="navbarNavAltMarkup"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span>
        <i class="fas fa-bars hamburger"></i>
      </span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
      <ul class="nav navbar-nav" :class="this.navClass">
        <li
          :class="!this.$root.isMobile ? 'spaceRight' : 'mobileSpacing'"
          v-on:click="collapseNav()"
        >
          <router-link to="/" class="navLink" v-scroll-to="'#about-copy'"
            >ABOUT</router-link
          >
        </li>
        <li
          :class="!this.$root.isMobile ? 'spaceRight' : 'mobileSpacing'"
          v-on:click="collapseNav()"
        >
          <router-link to="/resume" class="navLink">RESUME</router-link>
        </li>
        <li
          :class="!this.$root.isMobile ? 'spaceRight' : 'mobileSpacing'"
          v-on:click="collapseNav()"
        >
          <router-link to="/work" class="navLink">WORK</router-link>
        </li>
        <li
          :class="!this.$root.isMobile ? 'spaceRight' : 'mobileSpacing'"
          v-on:click="collapseNav()"
        >
          <router-link to="/playground" class="navLink">PLAYGROUND</router-link>
        </li>
        <li
          :class="!this.$root.isMobile ? 'spaceRight' : 'mobileSpacing'"
          v-on:click="collapseNav()"
        >
          <router-link to="/contact" class="navLink">CONTACT</router-link>
        </li>
      </ul>
    </div>
    <ul
      class="nav navbar-nav"
      :class="{ 'ml-auto': !this.$root.isMobile }"
      v-if="!this.$root.isMobile"
    >
      <li class="navIcon">
        <a href="https://github.com/JohnENoonan" target="_blank">
          <i class="fab fa-github"></i>
        </a>
      </li>
      <li class="navIcon">
        <a href="https://vimeo.com/noonanjohn" target="_blank">
          <i class="fab fa-vimeo"></i>
        </a>
      </li>
      <li class="navIcon">
        <a href="https://www.linkedin.com/in/john-noonan/" target="_blank">
          <i class="fab fa-linkedin"></i>
        </a>
      </li>
      <li class="navIcon">
        <a href="https://www.instagram.com/inbedatnoon/" target="_blank">
          <i class="fab fa-instagram"></i>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
import "bootstrap";
export default {
  data: function() {
    return {
      isOpen: false
    };
  },
  computed: {
    navClass: function() {
      if (!this.$root.isMobile) {
        return ["mr-auto", "spaceLeft"];
      } else {
        return ["centerText"];
      }
    },
    collapseBar: function() {
      return document.getElementById("navbarNavAltMarkup");
    }
  },
  methods: {
    toggleHamburger: function() {
      // console.log("toggle it");
      this.isOpen = !this.isOpen;
    },
    collapseNav: function() {
      this.collapseBar.className = this.collapseBar.className.replace(
        "show",
        "collapsing"
      );
      // $('#navbarNavAltMarkup').removeClass('show');
      // $('.collapse').collapse();
    }
  }
};
</script>

<style scoped src="bootstrap/dist/css/bootstrap.css"></style>
